* {
  font-family:Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  font-size: 1.2rem;
  width: 100%;
  color: grey;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  margin-top: 30px;
  overflow: hidden;
  width: 250px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 150px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.list {
  display: flex;
}